// NOTE: As of 2020.09, Omni UI requires a Constructable Style Sheets polyfill for Safari and
// Firefox browsers. See https://www.npmjs.com/package/construct-style-sheets-polyfill

import { styleSheetReplaceImport } from './omni-util.js';
import { stylesheet } from './stylesheet.js';

/**
 * An array of constructed stylesheets adoptable by Omni elements and apps
 */
const omniStyleSheets = [];
let omniStandardStyleSheet;

try {
  omniStandardStyleSheet = new CSSStyleSheet();
} catch (e) {
  // eslint-disable-next-line no-console
  console.error(
    `Omni UI: missing Constructable Style Sheets polyfill for Safari and Firefox. See https://www.npmjs.com/package/construct-style-sheets-polyfill`
  );
  throw e;
}

// CSS import at-rule resolved via rollup to a CSS string to streamline consumption requirements
// TODO(JE): Constructable Style Sheets polyfill doesn't throw errors, file issue
styleSheetReplaceImport(omniStandardStyleSheet, stylesheet)
  // eslint-disable-next-line no-console
  .catch(e => console.error(`omniStyleSheets error (${e})`));

omniStyleSheets.push(omniStandardStyleSheet);

export { omniStyleSheets };
